.App-header {  
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);  
}



.App-content {  
  display: flex;    
  flex-direction: column;    
  font-size: calc(10px + 2vmin);  
}


.videoitem{
  width: 80%;
   margin-left: 220px;
   margin-bottom: 50px;
}

.videoitem p{
   margin-bottom: 15px;
}

.videoitem img{
  width: 200px;
  height: 120px;
  margin-left: 30px;
  margin-bottom: 10px;
}

.assertlinks{
  font-size: large;
}

.download-links{
  padding-left: 20px;
  padding-bottom: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }

  
}
